@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&family=Work+Sans:wght@300;400;700;900&display=swap);
*{
  box-sizing: border-box;
}
body,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-family: 'Work Sans', sans-serif;
  color: #fff;
  font-weight: 900;
}

body {
  background:#5687E4 !important;
  height: 100vh;
}

code {
  font-family: 'Lato', sans-serif;
}

ul  {
  list-style: none;
  margin: 0;
  padding: 0 !important;
}

.loading-img {
  position: absolute;
  top: 70%;
  left: 50%;
  width: 100px;
  height: 100px;
  margin:-60px 0 0 -60px;
  -webkit-animation:spin 4s linear infinite;
  animation:spin 4s linear infinite;
}
@-webkit-keyframes spin { 
  100% { -webkit-transform: rotate(360deg); } 
}
@keyframes spin { 
  100% { 
      -webkit-transform: rotate(360deg); 
      transform:rotate(360deg); 
  } 
}

.App {
  text-align: center;
  background-color: #4369B2;
  color: #fff;
}



nav {
  background-color: #4369B2;
}
.back {
  border: none;
  background: transparent;
  color: #fff;
  width: 15%;
  text-align: start;
}

i:hover {
  cursor: pointer;
}
.country {
  height: 150px;
  width: 100%;
}

.c-img {
  -webkit-filter: invert(100%) sepia(0%) saturate(7483%) hue-rotate(353deg) brightness(97%) contrast(107%);
          filter: invert(100%) sepia(0%) saturate(7483%) hue-rotate(353deg) brightness(97%) contrast(107%);
}
button {
  background-color: transparent;
  border: none;
}
.stripe-text {
  font-size: 0.7rem;
  background-color: #35548B;
}

.search {
  width: 45%;
}
.header {
  background: #5687E4;
}
li:nth-child(4n+0) {
  background-color: #4b74c2;
}
li:nth-child(4n+1) {
  background-color: #4b74c2;
}
li:nth-child(4n+2) {
  background-color: #3E61A3;
}
li:nth-child(4n+3) {
  background-color: #3E61A3;
}

.map {
  -webkit-transform: scale(1);
          transform: scale(1);
}
tbody,
td,
tfoot,
th,
thead,
tr {
  border: none !important;
  height: 100px;
}

.pointer:hover {
  cursor: pointer;
}

tr:nth-child(even) {
 background-color: #35548B;
}
